import React, { Component } from 'react';
import Markdown  from 'react-markdown';




class JsPage extends Component {
    constructor(props) {
        super(props);
    }




    render() {

        return (
            <div>
                <Markdown source={" 1 chrome\n" +
                " > Shift+Command+F\n\n" +
                "2"}/>
                <div align="left"><img style={{maxHeight:"300px",height:"300px"}} src="data:image/jpeg;base64,"/></div>
            </div>
        );
    }
}

export default JsPage;


